@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&display=swap");

.overlay {
  text-align: center;
  /* font-family: "Press Start 2P", "VT323"; */
  font-family: "Open Sans";
  /* font-weight: 700; */
  color: rgb(51, 48, 48);
  z-index: 2;
  /* opacity: 0.85; */
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: fixed;
  /* pointer-events: none; */
}
/* ------------koko World------------ */
/* General Layout */
.kokoDao {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.kokoDao_container {
  display: flex; /* 使用 Flexbox 布局 */
  height: 100%; /* 使容器高度占满父容器 */
  width: 100%; /* 宽度占满父容器 */
  box-sizing: border-box;
  flex-direction: row; /* 设置为水平排列 */
  justify-content: space-between; /* 在两部分之间留出空间 */
  align-items: center; /* 上下居中 */
}

/* 提案列表 */
.kokoDao_proposallist {
  flex: 1; /* 左侧占用 50% 空间 */
  padding: 5px; /* 内边距 */
  height: 100%;
  border-right: 1px solid #dddddd39; /* 添加分隔线（可选） */
}

/* 操作区域 */
.kokoDao_operate_container {
  flex: 2; /* 右侧占用 50% 空间 */
  width: 100%;
  height: 100%;
  justify-content: center; /* 子元素上下居中 */
  align-items: center; /* 子元素水平居中 */
}
.kokoDao_operate {
  flex-direction: column; /* 子元素垂直排列 */
  padding: 60px 10px;
}
.DAO_title {
  margin-bottom: 30px;
  font-size: 30px;
  font-weight: 700;
  color: #333;
  text-shadow: 4px 4px 5px rgba(126, 124, 124, 0.518); /* 字体阴影 */
}
.kokoDao_operate input {
  color: #000000;
  font-family: inherit;
  width: 180px;
  height: 30px;
  font-size: 13px;
  font-weight: 600;
  text-align: center;
  padding: 5px;
  margin: 10px;
  border: 1px solid #cccccc; /* 添加边框线 */
  border-radius: 5px; /* 可选：使边框有圆角 */
}

.kokoDao_operate button {
  height: 30px;
  margin-bottom: 30px;
  background-color: var(--clr-blue);
  border-radius: 5px;
  color: white;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;

  padding: 0 15px;
}
.kokoDao_deploy,
.kokoDao_propose,
.kokoDao_vote,
.kokoDao_execute {
  margin: 10px;
}

/* ---------------proposal list------------- */
/* 提案容器样式 */
.proposal-container {
  display: flex; /* 使用 Flexbox 布局 */
  flex-direction: column; /* 垂直排列 */
  height: 100%; /* 高度为视口高度 */
  width: 100%; /* 宽度占满页面 */
}

/* 提案列表样式 */
.proposal_card_list {
  height: 80vh;
  margin-top: 10px;
  display: flex;
  flex-direction: column; /* 垂直排列卡片 */
  overflow-y: auto; /* 启用垂直滚动 */
  align-items: center; /* 垂直居中对齐 */
}
/* 提案列表滚动条样式 */
.proposal_card_list::-webkit-scrollbar {
  width: 8px;
}

.proposal_card_list::-webkit-scrollbar-thumb {
  background-color: #bbb;
  border-radius: 4px;
}

.proposal_card_list::-webkit-scrollbar-thumb:hover {
  background-color: #888;
}
/* 提案单独卡片样式 */
.proposal-card {
  border: 1px solid #e2e8f0;
  border-radius: 8px;
  padding: 15px;
  margin: 20px;
  max-width: 340px;
  background-color: #c7c7d971;
}
/* 刷新和 moveBlock 按钮区域样式 */
.refresh_moveblock {
  height: 8vh;
  display: flex;
  margin: 10px;
  justify-content: center; /* 按钮居中对齐 */
  align-items: center; /* 垂直居中对齐 */
}
/* 刷新按钮样式 */
.refresh-btn {
  height: 30px;
  background-color: #74729975;
  border-radius: 5px;
  color: white;
  font-size: 13px;
  font-weight: 700;
  margin-bottom: 10px;
  padding: 0px 15px;
}
/* voteStart时间戳样式 */
.proposal-timestamp {
  font-size: 13px;
  color: rgb(142, 141, 141);
}

/* 提案描述按钮样式 */
.proposal-description-btn {
  font-size: 19px;
  white-space: normal;
  word-break: break-word;
  font-weight: bold; /* 设置字体加粗 */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5); /* 添加阴影 */
}

/* 提案 ID 样式 */
.proposal-id {
  font-size: 13px;
  color: rgb(115, 115, 116);
  margin-top: 8px;
}

/* 提案时间状态样式 */
.proposal-status {
  font-size: 14px;
  color: rgb(196, 195, 195);
  margin-top: 20px;
}

.proposal-status.poll-ended {
  color: rgb(197, 116, 116);
}

.proposal-status.poll-in-progress {
  color: rgb(139, 164, 237);
}
.proposal-endtime {
  color: #7c7c7c;
  font-size: 12px;
  margin-top: 5px;
}
/* 进度条外层样式 */
.progress-bar {
  position: relative;
  width: 100%;
  height: 10px;
  border-radius: 8px;
  background-color: #e2e8f0;
  overflow: hidden;
  margin-top: 8px;
}
/* 支持 进度条 */
.progress-bar-support {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  background-color: #4c7fdd;
}
/* 反对 进度条样式 */
.progress-bar-oppose {
  position: absolute;
  top: 0;
  right: 0;
  height: 100%;
  background-color: #e26546;
}

/* 弃权 进度条样式 */
.progress-bar-abstain {
  position: absolute;
  top: 0;
  height: 100%;
  background-color: #ecc94b;
}

/* 支持统计数据样式 */
.support-statistics {
  display: flex;
  justify-content: space-between;
  margin-top: 8px;
}

.support-statistics-text {
  font-size: 13px;
  color: #879fcf;
}

/* 交易哈希按钮样式 */
.transaction-targets {
  font-size: 12px;
}
/* 默认链接样式 */
.transaction-targets a {
  text-decoration: none; /* 移除下划线 */
  color: #2a5c91e3; /* 设置链接的默认颜色 */
  font-weight: 700; /* 加粗字体 */
  transition: color 0.3s ease; /* 添加颜色变化的过渡效果 */
}

/* 鼠标悬停时的样式 */
.transaction-targets a:hover {
  color: #1c446f; /* 鼠标悬停时改变颜色 */
  text-decoration: underline; /* 鼠标悬停时显示下划线 */
}

/* 已访问链接样式 */
.transaction-targets a:visited {
  color: #6c757d; /* 已访问链接显示为灰色 */
}

/* 响应式布局：当屏幕宽度小于768px时调整布局 */
@media (max-width: 768px) {
  /* 容器 */
  .kokoDao_container {
    display: flex; /* 使用 Flexbox 布局 */
    height: 100%; /* 除去导航栏的高度 */
    width: 100%;
    box-sizing: border-box;
    flex-direction: column; /* 默认布局为上下排列 */
    overflow-y: auto; /* 容器滚动 */
  }

  /* 提案列表 */
  .kokoDao_proposallist {
    flex: none; /* 默认列表区域较大 */
    order: 2;
    height: auto; /* 高度占满容器 */
    padding: 20px; /* 内边距 */
  }
  .proposal_card_list {
    flex: none;
    height: auto;
    overflow-y: hidden; /* 启用垂直滚动 */
  }
  /* 操作区域 */
  .kokoDao_operate {
    flex: none; /* 操作区域占用剩余空间 */
    order: 1;
    padding: 30px; /* 内边距 */
    display: flex;
    flex-direction: column;
    align-items: center; /* 水平居中对齐 */
    justify-content: center; /* 垂直居中对齐 */
  }

  /* -----------弹窗 loading-----------*/

  .loading-overlay {
    z-index: 4;
    position: fixed; /* 将覆盖层固定在屏幕上 */
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* 半透明黑色背景 */
    display: flex; /* 使用 flexbox */
    justify-content: center; /* 水平居中 */
    align-items: center; /* 垂直居中 */
  }
  .loading-spinner {
    background-color: #fff; /* 背景色可以根据需要调整 */
    padding: 15px;
    font-size: 13px;
    border-radius: 10px; /* 可选，圆角 */
    color: #333; /* 字体颜色 */
    display: flex; /* 使用 flexbox */
    justify-content: center; /* 水平居中 */
    align-items: center; /* 垂直居中 */
    flex-direction: column; /* 垂直排列子元素 */
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2); /* 阴影效果 */
    animation: fadeIn 0.3s ease-out; /* 弹窗淡入动画 */
  }
  .loading-successmessage {
    background-color: #fff; /* 背景色可以根据需要调整 */
    width: auto; /* 容器宽度 */
    height: auto; /* 容器高度自动适应内容 */
    border-radius: 10px; /* 圆角 */
    display: flex; /* 使用 flexbox */
    flex-direction: column; /* 垂直排列子元素 */
    justify-content: flex-start; /* 内容从顶部开始排列 */
    align-items: flex-start; /* 左对齐 */
    padding: 30px; /* 内边距 */
  }
  .loading-successmessage h2 {
    font-size: 19px;
    margin: 10px;
  }
  .loading-successmessage p {
    font-size: 15px;
    margin: 10px;
  }

  /* 成功消息按钮样式 */
  .successmessage-button {
    width: 150px;
    height: 50px;
    margin: 20px;
    background-color: var(--clr-blue);
    color: var(--clr-white);
    border: none;
    border-radius: 5px; /* 更圆润的边角 */
    cursor: pointer;
    transition: all 250ms ease;
  }

  .successmessage-button:hover {
    background-color: var(--clr-blue-2); /* 按钮悬停效果 */
  }

  .successmessage-button:active {
    transform: scale(0.98); /* 按钮点击时缩放效果 */
  }

  /* 弹窗淡入动画 */
  @keyframes fadeIn {
    0% {
      opacity: 0;
      transform: scale(0.9);
    }
    100% {
      opacity: 1;
      transform: scale(1);
    }
  }
}
